<template>
  <div>
    <el-header class="header">
      <span>猎头</span>
      <span>User</span>
      <i @click="loginout" class="el-icon-switch-button"></i>
    </el-header>
    <el-container>
      <el-row class="radius">
        <el-form class="timeAndType" ref="form" label-width="80px">
          <el-form-item label="发布时间">
             <el-date-picker
            v-model="startDate"
            type="daterange"
            align="right"
            unlink-panels
            :default-time="['00:00:00','23:59:59']"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期" 
            value-format="timestamp">
            </el-date-picker>  
          </el-form-item>
          <!-- 类型表单下拉选择 -->
          <el-form-item label="类型">
            <el-col :span="24">
              <!-- :remote-method="remoteMethod"  远程搜索的方法-->
              <!-- :filter-method="TypeFilter"  自定义可搜索的方法 -->
              <!-- loading  是否正在从远程获取数据  loading-text 远程加载时显示的文字 -->
              <!-- 绑定空字符串，传值 -->
              <el-select 
              v-model="typevalue" 
              filterable
              clearable 
              placeholder="全部">
              <!-- 遍历数组 -->
                <el-option 
                v-for="typeitem in typeList" 
                :key="typeitem.value" 
                :label="typeitem.label" 
                :value="typeitem.value">
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <!-- 状态表单下拉选择 -->
          <el-form-item class="" label="状态">
            <el-col :span="24">
               <el-select 
              v-model="statusvalue" 
              clearable 
              placeholder="全部">
                <el-option 
                v-for="statusitem in statusList" 
                :key="statusitem.value" 
                :label="statusitem.label" 
                :value="statusitem.value">
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
        </el-form>
        <!-- </div> -->
        <!-- </el-col> -->
        <el-col class="el-col-button">
          <el-button class="button-clear-search" @click="clearData" type="danger">清空</el-button>
          <el-button class="button-clear-search" @click="search" type="success">搜索</el-button>
        </el-col>
      </el-row>
    </el-container>
    <el-container>
      <el-row class="Article-add">
        <p>Aritlcle</p>
        <router-link to="Add">
          <el-button class="add"  type="success">+新增</el-button>
        </router-link>
      </el-row>
    </el-container>
    <el-container class="bottom-radius">
      <el-table @row-click="handleEdit" :data="ReturnData" tooltip-effect="dark" border stripe style="width: 100%">
        <el-table-column type="index" prop="id" label="ID"></el-table-column>
        <el-table-column prop="title" label="名称"></el-table-column>
        <el-table-column prop="type" label="类型"></el-table-column>
        <el-table-column :formatter="dateFormat" prop="createAt" label="发布时间"></el-table-column>
        <el-table-column :formatter="dateFormat" prop="updateAt" label="修改时间"></el-table-column>
        <el-table-column prop="author" label="发布者"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
        <!-- 在表格里面修改状态的过滤器，表格组件自带过滤器的内容，不要直接写一个过滤器放在里面会失效，用组件里面的，先把数据提取出来，然后进行过滤修改，就可以了 -->
        <el-table-column  class="handle" prop="status" label="上下线">
          <template slot-scope="scope">
            <!-- 将状态的数据修改为上线或者下线，然后发送请求 -->
            <span class="handle" @click="editstatus(scope.row.id,scope.row.status)">{{scope.row.status | statusFilter}}</span>
          </template>

        </el-table-column>

        <el-table-column class="handle" prop="" label="操作">
          <template slot-scope="scope">
            
            <span size="mini" class="handle" @click="deletedata">删除</span>
            <router-link :to="editRouter"><span class="handle" @click="edit(scope.row.id)">编辑</span></router-link>
            
          </template>
           
        </el-table-column>
      </el-table>
    </el-container>
    <div class="block">
      <!-- handleSizeChange是显示每页多少条
          handleCurrentChange 是当前第几页
       -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage1"
        :page-sizes="[10,20,30,40,50]" :page-size="10" layout="total,sizes,prev, pager, next, jumper,->,total,slot"
        :total='result.total'>
      </el-pagination>
    </div>

  </div>
</template>

<script>
import  New from '@/new'
  export default {
    name: 'ArticleList',
    data() {
      return {
        typeList: [{//用来存放类型的数组，需要遍历渲染
            value: '',
            label: '全部'
          },
          {
            value: '0',
            label: '首页HomePage'
          }, {
            value: '1',
            label: '找职位SeekJob'
          }, {
            value: '2',
            label: '找精英Find the elite'
          }, {
            value: '3',
            label: '行业大图'
          }
        ],
        statusList:[//用来存放状态的数组，需要遍历渲染
          {
            value:"",
            label:"全部"
          },
            {
            value:"1",
            label:"草稿"
          },
            {
            value:"2",
            label:"上线"
          },
          ],
        typevalue: '',//搜索类型时候，根据输入值的不用，来存值
        statusvalue:'',//状态的存值点
        click:0,
        startDate:'',//创建时间的存值，用来绑定创建时间的选择
        endDate:'',//修改时间的存值，用来绑定修改时间的选择
        currentPage1: 0, //默认的开始页
        ReturnData: [], //后台返回的数据,用这个数组来接收
        result: {}, //用来接收后台传过来的对象
        pageval:'',//分页的数值存放点
        sizeval:'',//每页显示数目的存值点
        onlinehandle:'',//上下线操作的存值点
        idvalue:'',//id的存值位置
        editRouter:'',//点击编辑跳转到哪个路由的存放点

        editTitlevValue:'',//获取的每个id的主题的存放位置
        editStatusValue:'',//获取的每个id的状态的存放位置
        editImgValue:'',//获取的每个id的图片的存放位置
        editUrlValue:'',//获取的每个id的跳转链接的存放位置
        editCreatAt:'',//获取的每个id的创建时间的额存放位置
        editTypeValue:'',//获取的每个id的类型的值的存放位置
        editDesc:'',//获取每个id的说明的值的存放位置
        editIndustryValue:'',//获取的每个id的行业大图的类型的存放位置
        id:'',
       
     }
    }, 
    
    methods: {
      search(key) {//搜索事件的请求
        console.log(this.startDate)
        if(this.startDate===0||this.startDate===null){
          this.startDate=''
        }
          this.$axios.get('api/a/article/search',{
            params:{
              type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
              startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page:this.pageval,//发送页码的参数
              size:this.sizeval//发送每页显示数目的参数
            },
          })
          .then(res=>{//当请求成功的时候执行下面的内容
            // console.log('请求成功了')
            // console.log(res)
            // console.log(res.data.data.articleList)
            console.log("请求到数据了")
            var result = res.data.data
            // console.log(result)
            this.ReturnData = result.articleList
            this.result = result
            
            // console.log(typeof this.ReturnData)
            //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
            for (var i = 0; i < this.ReturnData.length; i++) {
              // console.log(this.ReturnData[i].status)
              if (this.ReturnData[i].status === 1) {
                this.ReturnData[i].status = "草稿"
               
              }
              if (this.ReturnData[i].status === 2) {
                this.ReturnData[i].status = "上线"
              }
              if (this.ReturnData[i].type === 0) {
                this.ReturnData[i].type = "首页HomePage"
              }
              if (this.ReturnData[i].type === 1) {
                this.ReturnData[i].type = "找职位SeekJob"
              }
              if (this.ReturnData[i].type === 2) {
                this.ReturnData[i].type = "找精英Find the elite"
              }
              if (this.ReturnData[i].type === 3) {
                this.ReturnData[i].type = "行业大图"
              }
             
            }
          })
          .catch(err=>{
            console.log('请求失败了')
          })
          this.click++;
          console.log("click等于"+this.click)
          //如果三个选项都没有选择的话，那么使得在搜索点击事件中添加的
          if (this.typevalue==''&&this.statusvalue==''&&this.startDate=='') {
            this.click=0
          }
   
      },



      //清除按钮的功能
      clearData(){
        this.typevalue='',
        this.statusvalue=''
        this.startDate=''
      },



      //在选择每页多少条的时候触发
      handleSizeChange(EveryPageNum) {
        console.log(this.result)
        console.log(EveryPageNum),
        console.log(`每页 ${EveryPageNum} 条`);
          this.sizeval = EveryPageNum
          if(this.startDate===null){
          this.startDate=''
        }
        if (this.click===0) {
          this.typevalue='',
          this.statusvalue='',
          this.startDate=''
        }
      
          this.$axios.get('api/a/article/search', {
            params: {
              type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
               startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page: this.pageval,  //根据点击的页码，发送页码的参数
              size:this.sizeval, //每页显示数目的参数，
            }
          })
          .then(res => {
            // console.log(res)
            // console.log(res.data.data.articleList)
            console.log("请求到数据了")
            var result = res.data.data
            // console.log(result)
            this.ReturnData = result.articleList
            this.result = result
            // console.log(typeof this.ReturnData)
            //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
            for (var i = 0; i < this.ReturnData.length; i++) {
              // console.log(this.ReturnData[i].status)
              if (this.ReturnData[i].status === 1) {
                this.ReturnData[i].status = "草稿"
              }
              if (this.ReturnData[i].status === 2) {
                this.ReturnData[i].status = "上线"
              }
              if (this.ReturnData[i].type === 0) {
                this.ReturnData[i].type = "首页HomePage"
              }
              if (this.ReturnData[i].type === 1) {
                this.ReturnData[i].type = "找职位SeekJob"
              }
              if (this.ReturnData[i].type === 2) {
                this.ReturnData[i].type = "找精英Find the elite"
              }
              if (this.ReturnData[i].type === 3) {
                this.ReturnData[i].type = "行业大图"
              }
            }
          })
          .catch(err => {
            console.log("没请求到")
            console.log(err)
          })
      },
      //在点击分页第几页的时候触发


      //当点击不同的分页时触发这个钩子
      handleCurrentChange(val) {
        this.pageval = val
        if(this.startDate===null){
          this.startDate=''
        }
        if (this.click===0) {
          this.typevalue='',
          this.statusvalue=''
          this.startDate=''
        }
        console.log(`当前页: ${val}`);
        console.log(val),
          this.$axios.get('api/a/article/search', {
            params: {
              type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
               startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page: this.pageval,  //根据点击的页码，发送页码的参数
              size:this.sizeval,//每页显示数目的参数
            }
          })
          .then(res => {
            console.log(res)
            console.log(res.data.data.articleList)
            console.log("请求到数据了")
            var result = res.data.data
            console.log(result)
            this.ReturnData = result.articleList
            this.result = result
            console.log(typeof this.ReturnData)
            //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
            for (var i = 0; i < this.ReturnData.length; i++) {
              console.log(this.ReturnData[i].status)
              if (this.ReturnData[i].status === 1) {
                this.ReturnData[i].status = "草稿"
              }
              if (this.ReturnData[i].status === 2) {
                this.ReturnData[i].status = "上线"
              }
              if (this.ReturnData[i].type === 0) {
                this.ReturnData[i].type = "首页HomePage"
              }
              if (this.ReturnData[i].type === 1) {
                this.ReturnData[i].type = "找职位SeekJob"
              }
              if (this.ReturnData[i].type === 2) {
                this.ReturnData[i].type = "找精英Find the elite"
              }
              if (this.ReturnData[i].type === 3) {
                this.ReturnData[i].type = "行业大图"
              }
            }
          })
          .catch(err => {
            console.log("没请求到")
            console.log(err)
          })

   

      },



      //从后端获取的时间戳数据，格式化的方法
      //当哪一列需要用到的时候，就在哪一列 :formatter这个函数，就可以将时间戳转换为时间
      dateFormat: function (row, column) {
        // console.log(row, column)
        const date = row[column.property]
        if (date === undefined) {
          return ''
        }
        // # 这里的格式根据需求修改
        return this.$moment(date).format('YYYY-MM-DD HH:mm:ss')
      },

     

      //点击获取当前行数据的钩子
      handleEdit(row){
        console.log(row)
        this.idvalue = row.id
        this.editTitlevValue = row.title
        this.editStatusValue = row.status
        this.editImgValue = row.img
        this.editUrlValue = row.url
        this.editCreateAt  =row.createAt
        this.editTypeValue = row.type
        this.editIndustryValue = row.industry

        console.log(this.onlinehandle)
        console.log(this.idvalue)
      },

       //删除操作的钩子
      deletedata(){
        console.log(this.idvalue)
          this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        this.$axios.delete('api/a/u/article/'+this.idvalue,)//删除的请求，当在弹出框点击确定之后
        .then(res=>{
          console.log(res)
          this.$axios.get('api/a/article/search', {//当删除请求成功之后，再次请求列表的数据，重新渲染列表
          params: {
            type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
              startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page:this.pageval,//发送页码的参数
              size:this.sizeval,//发送每页显示数目的参数
              
          }
        })
        .then(res => { //当请求成功的时候执行下面的内容
          // console.log(res)
          // console.log(res.data.data.articleList)
          console.log("请求到数据了")
          var result = res.data.data
          // console.log(result)
          this.ReturnData = result.articleList
          this.result = result
          // console.log(this.ReturnData.id)
          //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
          for (var i = 0; i < this.ReturnData.length; i++) {
            // console.log(this.ReturnData[i].status)
            if (this.ReturnData[i].status === 1) {
              this.ReturnData[i].status = "草稿"
            }
            if (this.ReturnData[i].status === 2) {
              this.ReturnData[i].status = "上线"
            }
            if (this.ReturnData[i].type === 0) {
              this.ReturnData[i].type = "首页HomePage"
            }
            if (this.ReturnData[i].type === 1) {
              this.ReturnData[i].type = "找职位SeekJob"
            }
            if (this.ReturnData[i].type === 2) {
              this.ReturnData[i].type = "找精英Find the elite"
            }
            if (this.ReturnData[i].type === 3) {
              this.ReturnData[i].type = "行业大图"
            }
          }
        })
        .catch(err => { //当请求错误的时候执行
          console.log("没请求到")
          console.log(err)
        })
        })
        .catch(err=>{
          console.log(err)
        })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },

      //修改状态的上下线的操作
        editstatus(val1,val2){
          console.log(val1+"分割线",val2)
          
          //点击进行上线状态的改变
        if(val2=="草稿"){
          this.$confirm('是否执行上线操作?','操作提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios.put('api/a/u/article/status',
            this.$qs.stringify({
              id:val1,
              status:2,
            }))
            .then(res=>{
              console.log(res)
              console.log("请求成功了")
              this.$axios.get('api/a/article/search', {//当删除请求成功之后，再次请求列表的数据，重新渲染列表
          params: {
            type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
              startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page:this.pageval,//发送页码的参数
              size:this.sizeval,//发送每页显示数目的参数
          }
        })
        .then(res => { //当请求成功的时候执行下面的内容
          // console.log(res)
          // console.log(res.data.data.articleList)
          console.log("请求到数据了")
          var result = res.data.data
          // console.log(result)
          this.ReturnData = result.articleList
          this.result = result
          // console.log(this.ReturnData.id)
          //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
          for (var i = 0; i < this.ReturnData.length; i++) {
            // console.log(this.ReturnData[i].status)
            if (this.ReturnData[i].status === 1) {
              this.ReturnData[i].status = "草稿"
            }
            if (this.ReturnData[i].status === 2) {
              this.ReturnData[i].status = "上线"
            }
            if (this.ReturnData[i].type === 0) {
              this.ReturnData[i].type = "首页HomePage"
            }
            if (this.ReturnData[i].type === 1) {
              this.ReturnData[i].type = "找职位SeekJob"
            }
            if (this.ReturnData[i].type === 2) {
              this.ReturnData[i].type = "找精英Find the elite"
            }
            if (this.ReturnData[i].type === 3) {
              this.ReturnData[i].type = "行业大图"
            }
          }
        })
        .catch(err => { //当请求错误的时候执行
          console.log("没请求到")
          console.log(err)
        })
            })
            .catch(err=>{
              console.log(err)
              console.log("请求失败了")
            })

          this.$message({
          type: 'success',
          message: '上线成功!'
          });
        }).catch(() => {
          this.$message({
          type: 'info',
          message: '取消上线操作'
          });          
        });
        }
        //点击进行下线状态的改变
          if(val2=="上线"){
          this.$confirm('是否执行下线操作?','操作提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios.put('api/a/u/article/status',
            this.$qs.stringify({
              id:val1,
              status:1,
            }))
            .then(res=>{
              console.log(res)
              console.log("请求成功了")
              this.$axios.get('api/a/article/search', {//当删除请求成功之后，再次请求列表的数据，重新渲染列表
          params: {
            type:this.typevalue,//发送类型的参数进行请求
              status:this.statusvalue,//发送状态的参数进行请求
              startAt:this.startDate[0],//发送起始更新时间的参数
              endAt:this.startDate[1],//发送终止更新时间的参数
              page:this.pageval,//发送页码的参数
              size:this.sizeval,//发送每页显示数目的参数
          }
        })
        .then(res => { //当请求成功的时候执行下面的内容
          // console.log(res)
          // console.log(res.data.data.articleList)
          console.log("请求到数据了")
          var result = res.data.data
          // console.log(result)
          this.ReturnData = result.articleList
          this.result = result
          // console.log(this.ReturnData.id)
          //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
          for (var i = 0; i < this.ReturnData.length; i++) {
            // console.log(this.ReturnData[i].status)
            if (this.ReturnData[i].status === 1) {
              this.ReturnData[i].status = "草稿"
            }
            if (this.ReturnData[i].status === 2) {
              this.ReturnData[i].status = "上线"
            }
            if (this.ReturnData[i].type === 0) {
              this.ReturnData[i].type = "首页HomePage"
            }
            if (this.ReturnData[i].type === 1) {
              this.ReturnData[i].type = "找职位SeekJob"
            }
            if (this.ReturnData[i].type === 2) {
              this.ReturnData[i].type = "找精英Find the elite"
            }
            if (this.ReturnData[i].type === 3) {
              this.ReturnData[i].type = "行业大图"
            }
          }
        })
        .catch(err => { //当请求错误的时候执行
          console.log("没请求到")
          console.log(err)
        })
            })
            .catch(err=>{
              console.log(err)
              console.log("请求失败了")
            })

          this.$message({
          type: 'success',
          message: '下线成功!'
          });
        }).catch(() => {
          this.$message({
          type: 'info',
          message: '取消下线操作'
          });          
        });
        }
        },


      //编辑操作的钩子
      edit(val1){
        this.editRouter = "Add"
        this.$axios.get('api/a/article/'+val1)
        .then(res=>{
          console.log(res.data.data.article)
          this.editTitlevValue = res.data.data.article.title
          this.editCreatAt = res.data.data.article.createAt
          if(res.data.data.article.type==0){
            res.data.data.article.type="首页HomePage"
          }if(res.data.data.article.type==1){
            res.data.data.article.type="找职位SeekJob"
          }if(res.data.data.article.type==2){
            res.data.data.article.type="找精英Find the elite"
          }if(res.data.data.article.type==3){
            res.data.data.article.type="行业大图"
          }
          if(res.data.data.article.industry==0){
            res.data.data.article.industry="移动互联网"
          } if(res.data.data.article.industry==1){
            res.data.data.article.industry="电子商务"
          } if(res.data.data.article.industry==2){
            res.data.data.article.industry="企业服务"
          } if(res.data.data.article.industry==3){
            res.data.data.article.industry="O2O"
          } if(res.data.data.article.industry==4){
            res.data.data.article.industry="教育"
          } if(res.data.data.article.industry==5){
            res.data.data.article.industry="金融"
          } if(res.data.data.article.industry==6){
            res.data.data.article.industry="游戏"
          } if(res.data.data.article.industry==""){
            res.data.data.article.industry=""
          }

          this.editTypeValue = res.data.data.article.type
          this.editIndustryValue = res.data.data.article.industry
          this.editUrlValue = res.data.data.article.url
          this.editImgValue = res.data.data.article.img
          this.editDesc = res.data.data.article.content
          this.id = res.data.data.article.id
          New.$emit('getEditData',{
            title:this.editTitlevValue,
            createAt:this.editCreatAt,
            type:this.editTypeValue,
            industry:this.editIndustryValue,
            content:this.editDesc,
            url:this.editUrlValue,
            img:this.editImgValue,
            id:this.id
          })
        })
        .catch(err=>{
          console.log(err)
        })
      },
      //退出登录
      loginout(){
        this.$axios.post('api/a/logout',)
        .then(res=>{
          console.log(res)
          this.$router.push({path:'/Login'})
        })
        .catch(err=>{
          console.log(err)
        })
      }
    },



    //向接口发送列表表格的请求
    mounted() {
      //发送请求，请求列表的数据
      this.$axios.get('api/a/article/search', {
          params: {
            page: 1 //首先请求到第一页
          }
        })
        .then(res => { //当请求成功的时候执行下面的内容
          console.log(res)
          // console.log(res.data.data.articleList)
          console.log("请求到数据了")
          var result = res.data.data
          // console.log(result)
          this.ReturnData = result.articleList
          this.result = result
          console.log(result)
          // console.log(this.ReturnData.id)
          //遍历获取到的数据的数组，来修改返回的类型和状态，改成字符串
          for (var i = 0; i < this.ReturnData.length; i++) {
            // console.log(this.ReturnData[i].status)
            if (this.ReturnData[i].status === 1) {
              this.ReturnData[i].status = "草稿"
            }
            if (this.ReturnData[i].status === 2) {
              this.ReturnData[i].status = "上线"
            }
            if (this.ReturnData[i].type === 0) {
              this.ReturnData[i].type = "首页HomePage"
            }
            if (this.ReturnData[i].type === 1) {
              this.ReturnData[i].type = "找职位SeekJob"
            }
            if (this.ReturnData[i].type === 2) {
              this.ReturnData[i].type = "找精英Find the elite"
            }
            if (this.ReturnData[i].type === 3) {
              this.ReturnData[i].type = "行业大图"
            }
          }
        })
        .catch(err => { //当请求错误的时候执行
          console.log("没请求到")
          console.log(err)
        })
    },
    filters:{
      statusFilter(val){
        if(val=="上线"){
          val='下线'
          return  val
        }else{
          val="上线"
          return val
        }

      }
    }
  }

</script>

<style scoped>
  .header {
    width: 99%;
    text-align: right;
    font-size: 12px;
    background: #fbfbfb;
  }

  /* 上面行的边框 */
  .radius {
    width: 75vw;
    margin: 2rem;
    /* height: 12rem; */
    border-radius: 4px;
    border: 1px solid #c4c4c4;
  }

  /* 选择框行的样式 */
  .timeAndType {
    display: flex;
    justify-content: space-around;
    align-self: center;
    margin: 3rem 2rem 1rem;
  }

  .line {
    text-align: center;
  }

  .el-col-button {
    text-align: right;
    padding-right: 2rem;
  }

  .el-button--danger {
    background: #d8524e;
  }

  .bottom-radius {
    width: 75vw;
    margin: 0 2rem;
    /* height: 33rem; */
    border-radius: 4px;
    border: 1px solid #c4c4c4;
    border-top: 0;
  }

  .button-clear-search {
    padding: 10px;
    margin-bottom: 2rem;
  }

  .Article-add {
    display: flex;
    align-items: center;
    width: 100%;
    border: 1px solid#c4c4c4;
    margin: 0 2rem;
    padding-left: 10px;
    font: bold;
  }

  .add {
    position: absolute;
    bottom: 0.8rem;
    right: 2rem;
    font-size: 12px;
    padding: 5px;
  }

  .block {
    text-align: end;
    margin: 1rem 2rem;
  }
  .handle{
    color: #337ab7;
    cursor: pointer;
    margin: 0 10px;
  }
  .handle:hover{
    color: #333333;
  }
</style>



