<template>
  <div id="login">
    <div class="record">

      <h2>后台登录</h2>
      <div class="one">
        <img src="../assets/SAAS-客户管理.png" />
        <input id="name" type="text" placeholder="用户名" onkeyup="value=value.replace(/[\u4E00-\u9FA5]/g,'')"
          v-model="user" @keyup.enter="login" />
      </div>
      <div class="one">
        <img src="../assets/密码.png" height="200" width="200" />
        <input id="password" type="password" placeholder="密码" onkeyup="value=value.replace(/[\u4E00-\u9FA5]/g,'')"
          v-model="pwd" @keyup.enter="login" />
      </div>
      <p class="now">{{msg}}</p>
      <!-- <router-link to="/List"> -->
      <button @click="login">登录</button>
      <!-- </router-link> -->
      <!-- <List></List> -->
      <!-- <router-view></router-view> -->
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        user: '',
        pwd: '',
        msg: ''
      }
    },
    // components:{
    //   List
    // },
    methods: {
      login() {
        // console.log(this.pwd);
        // console.log(this.user);
        // const name=this.user
        // const pswd = this.pwd
        this.$axios.post("/api/a/login",
            this.$qs.stringify({
              name: this.user, //获取input输入的用户名
              pwd: this.pwd //获取input输入的密码
            }))
          .then(res => {
            console.log(res.data) //输出从服务器获取到的数据
            this.msg = res.data.message //修改msg的内容，输出
            // this.$router.push({path:'/List'})

            const isLogin = res.data.code //定义code，成功0，不成功是别的
            if (isLogin === 0) {
              console.log("成功")
              this.$router.push({
                path: '/List'
              }) //如果code=0，则跳转页面
              // console.log(res.data.data.manager.name,res.data.data.role.name)
            } else {
              console.log("失败")
            }


          })
          .catch(err => {
            console.log(err) //输出请求错误
          })
        // return this.res
      },
    }

  }

</script>

<style scoped lang="scss" type="text/css" scoped>
  #login {
    background: url("../assets/bg.png") repeat;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100vh;

    .record {
      margin-top: 280px;
      width: 30%;
      min-width: 320px;
      height: 280px;
      background: #d7d7d7;
      opacity: 0.6;
      text-align: center;

      h2 {
        text-align: center;
        color: #29bde0;
      }

      .one {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px;
        background: #fff;
        height: 2rem;

        input {
          width: 80%;
          outline: none;
          margin-left: 10px;
          border: 0;
          border-left: 1px solid;
          padding-left: 10px;
        }
      }

      img {
        width: 25px;
        height: 25px;
      }

      button {
        width: 80%;
        height: 3rem;
        background: deepskyblue;
        border-radius: 10px;
        text-align: center;
        cursor: pointer;
      }

      p {
        margin: 0;
        height: 1.9rem;
      }
    }
  }
</style>



