import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import App from '@/App'
import List from '@/views/List'
import Login from '@/views/Login'
import Add from '@/views/Add'
import Welcome from '@/views/Welcome'
import ArticleList from '@/views/ArticleList'
Vue.use(Router)




export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
     {
      path:'/Login',
      name:'Login',
      component:Login
    },
    {
      path:'/List',
      name:'List',
      component:List,
      children:[
        {
        path:'',
        name:'Welcome',
        component:Welcome
      },
      {
        path:'',
        name:'Welcome',
        component:Welcome
      },
      {
        path:'ArticleList',
        name:'ArticleList',
        component:ArticleList,
        
      },
      {
        path:'add',
        name:'Add',
        component:Add
      }
    ]
    },
   
   
  ]
})




