<template>
  <div id="Add" class="Add">
    <el-container>
      <el-row>
        <el-col>
          <el-header class="header">
            <span>猎头</span>
            <span>User</span>
            <i @click="loginout" class="el-icon-switch-button"></i>
          </el-header>
        </el-col>
      </el-row>
    </el-container>
    <el-container>
      <el-row class="Article-add">
        <p>新增Aritlcle</p>
      </el-row>
    </el-container>
    <el-container>
      <el-row class="radius">
        <el-col>
          <el-form ref="form" label-width="80px">
            <el-form-item class="inputWidth" label="标题名称">
              <el-input v-model="title"></el-input>
            </el-form-item>
            <!-- 当选择类型的时候,遍历数组, -->
            <el-form-item class="inputWidth" label="类型">
               <el-select 
              v-model="typevalue" 
              clearable 
              placeholder="全部">
              <!-- <span>  {{aaa}} </span> -->
             
              <!-- 遍历数组 -->
                <el-option 
                v-for="typeitem in typeList" 
                :key="typeitem.value" 
                :label="typeitem.label" 
                :value="typeitem.value">
               <!-- {{1|typeFilter}} -->
               <!-- {{typeitem.value|typeFilter}} -->
                </el-option>
              </el-select>
            </el-form-item>


            <!-- 当选择行业大图时，要选择图片的类型 -->
            <el-form-item v-if="this.typevalue==3||this.typevalue=='行业大图'" class="inputWidth">
               <el-select 
              
              v-model="picvalue" 
              clearable 
              placeholder="全部">
              <!-- 遍历数组 -->
                <el-option 
                v-for="pic in picType" 
                :key="pic.value" 
                :label="pic.label" 
                :value="pic.value">
                </el-option>
              </el-select>
            </el-form-item>



            <el-form-item class="inputWidth" label="说明">
              <el-input type="textarea" v-model="desc"></el-input>
            </el-form-item>
            <el-form-item class="inputWidth" label="跳转链接">
              <el-input v-model="link"></el-input>
            </el-form-item>
            <el-form-item class="inputWidth" label="配图">
              <el-upload
                class="upload-demo"
                ref="upload"
                action="api//a/u/img/task"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-change="filedata"
                :auto-upload="false"
                :limit="1"
                :on-success="selectsuccess"
                :before-remove="beforeRemove"
                :on-exceed="handleExceed"
                
                list-type="picture">
                <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
            </el-upload>  
              <img class="imgurl" :src="imgurl">
              <div class="handleName">
                <div>图片名</div>
                <div>文件大小</div>
                <div>操作</div>
                <div>操作</div>
              </div>
              <div class="handleStyle">
                <div>{{file.name}}</div>
                <div>{{file.size}}</div>
                <div><i class="el-icon-check successIcon" v-if="this.uploadSuccessData.code===0"></i></div>
                <div><el-button style="" size="mini" type="success" @click="submitUpload" v-if="this.file!=''">上传</el-button></div>
              </div>
            </el-form-item>
            <el-form-item >
              <el-button-group class="bottom-btn-center">
              <router-link :to="this.skipLink"><el-button class="left-button" type="primary" @click="draftSubmit">存为草稿</el-button></router-link>
              <router-link :to="this.skipLink"><el-button class="left-button" type="primary" @click="onlineSubmit">立即上线</el-button></router-link>
             <router-link to="ArticleList"><el-button  @click="cancel">取消</el-button></router-link> 
              </el-button-group>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-container>
  </div>
</template>

<script>
import New from '@/new'
  export default {
    data() {
      return {
           typeList: [{//用来存放类型的数组，需要遍历渲染
            value: '',
            label: '全部'
          },
          {
            value: '0',
            label: '首页HomePage'
          }, {
            value: '1',
            label: '找职位SeekJob'
          }, {
            value: '2',
            label: '找精英Find the elite'
          }, {
            value: '3',
            label: '行业大图'
          }
        ],
        picType:[
          {
            value:"",
            label:'请选择'
          },
          {
          value:"0",
          label:'移动互联网'
        },
          {
          value:"1",
          label:'电子商务'
        },
          {
          value:"2",
          label:'企业服务'
        },
          {
          value:"3",
          label:'O2O'
        },
          {
          value:"4",
          label:'教育'
        },
          {
          value:"5",
          label:'金融'
        },
          {
          value:"6",
          label:'游戏'
        }
        ],
        typevalue: '',//搜索类型时候，根据输入值的不用，来存值
        picvalue:'',//行业大图的类型存值
        file:'',//上传的文件空列表，在上传文件时会被调用
        uploadSuccessData:'',//上传文件成功时返回的数据的存放位置，为了让对号显示出来
        title:'',//标题名称的存放位置
        type:'',//类型数据的存放位置
        desc:'',//说明的存放位置
        link:'',//跳转链接的存放位置
        imgurl:'',//上传的图片的链接，用来新增给后端上传
        industry:'',
        createAt:'',
        onlinestatus:'2',
        draftstatus:1,
        skipLink:"",
        id:'',
        uesr:'',
        role:''
      }
    }, 
    methods: {
      //上传到服务器的点击事件
      submitUpload(file) {
        this.$refs.upload.submit();
        console.log('我点击了上传按钮')
      },
      //移除的点击事件
      handleRemove(file, fileList) {
        console.log('删除了')
        if(this.uploadSuccessData){
           this.uploadSuccessData.code=''
        }
      },
      //点击上传的文件的事件
      handlePreview(file) {
        console.log('我点击了上传的文件');
      },
      //用在on-change，文件状态改变时的钩子，当添加文件，上传成功，和上传失败时都会被调用
      filedata(file){
        //将上传文件的size，从字节换算成 M （兆字节），并且使用parseFloat(value).toFixed(num)方法，
              //将换算之后的值，更改为小数点之后两位
        //如果图片的大小大于0，那么这个数字那么就证明尺寸是上传的字节，先执行上面的
        if(file.size>0){
            file.size=parseFloat(file.size/1024/1024).toFixed(2)+'M'
          }else{//当点击上传之后，会再次调用这个钩子，这个时候的大小因为在上面执行了 + “M” 那么变为了字符串，那么就执行下面的
            // file.size=parseFloat(file.size).toFixed(2)+'M'
            file.size=file.size
            }
        this.file = file
        
        console.log('文件状态改变了')
      },
      //当文件上传成功时调用的钩子，第一个参数是服务器返回的参数，第二个参数是上传的文件，第三个是上传的文件的列表
     selectsuccess(response, file, fileList){
      //  file.size=parseFloat(file.size/1024/1024).toFixed(2)+'M'
      //  this.file = file
      this.imgurl=response.data.url
      console.log(this.imgurl)
       this.uploadSuccessData = response
       console.log('文件上传成功了')
     },
     //当超出限制的个数时，调用这个钩子
        handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      //删除之前的钩子,点击删除按钮之后，在删除之前 
      beforeRemove(file){
        console.log(file+"删除之前的钩子文件")
        
      },
      //立即上线，存为草稿的提交的事件
      onlineSubmit() {//立即上线的点击按钮
        console.log('立即上线');
        if(this.createAt>0){
          if(this.typevalue=="首页HomePage"){
          this.typevalue=0
        }if(this.typevalue=="找职位SeekJob"){
          this.typevalue=1
        }if(this.typevalue=="找精英Find the elite"){
          this.typevalue=2
        }if(this.typevalue=="行业大图"){
          this.typevalue=3
        } 
        if(this.picvalue=="移动互联网"){
          this.picvalue=0
        }if(this.picvalue=="电子商务"){
          return this.picvalue=1
        }if(this.picvalue=="企业服务"){
          this.picvalue=2
        }if(this.picvalue=="O2O"){
           this.picvalue=3
        }if(this.picvalue=="教育"){
           this.picvalue=4
        }if(this.picvalue=="金融"){
          this.picvalue=5
        }if(this.picvalue=="游戏"){
           this.picvalue=6
        }if(this.picvalue==''){
          this.picvalue=''
        }
          this.$axios.put('api/a/u/article/'+this.id,
          this.$qs.stringify({
              title:this.title,
              status:2,
              img:this.imgurl,
              url:this.link,
              createAt:this.createAt,
              type:this.typevalue,
              industry:this.picvalue,
              content:this.desc,
          }))
          .then(res=>{
            console.log(res)
            
          })
          .catch(err=>{
            console.log(err)
          })
        this.skipLink = "ArticleList"
        this.$alert('编辑上线成功', {
          confirmButtonText: '确定',
        })

        }else{
           if(this.title!=''&&this.typevalue!=''&&this.imgurl!=''&&this.desc!=''&&this.link!=''){
        this.$axios.post('api/a/u/article',
        this.$qs.stringify({
        title:this.title,
        type:this.typevalue,
        status:this.onlinestatus,
        img:this.imgurl,
        content:this.desc,
        url:this.link,
        industry:this.picvalue,
        }))
        .then(res=>{
          console.log(res)
          
        })
        .catch(err=>{
          console.log(err)
        })
        this.skipLink = "ArticleList"//当我们所有的内容都输入完之后，再点击上线按钮，才会将路由跳转的值添加上，才会跳转路由
         this.$alert('新增上线成功', {//这是弹出警示框的组件，我们还可以在组件内添加更多的内容，这里用不到，就删除了
          confirmButtonText: '确定',
        });
        }else{
           this.$alert('请完整的填写信息再提交', {
          confirmButtonText: '确定',
        })
        }
        }

       

      },


      draftSubmit(){//存为草稿的点击按钮
        console.log('存为草稿')
        console.log(this.typevalue)
        console.log(this.picvalue)
        console.log(this.imgurl)
        console.log(this.createAt)
    
        if(this.createAt>0){
            if(this.typevalue=="首页HomePage"){
          this.typevalue=0
        }if(this.typevalue=="找职位SeekJob"){
          this.typevalue=1
        }if(this.typevalue=="找精英Find the elite"){
          this.typevalue=2
        }if(this.typevalue=="行业大图"){
          this.typevalue=3
        } 
        if(this.picvalue=="移动互联网"){
          this.picvalue=0
        }if(this.picvalue=="电子商务"){
          return this.picvalue=1
        }if(this.picvalue=="企业服务"){
          this.picvalue=2
        }if(this.picvalue=="O2O"){
           this.picvalue=3
        }if(this.picvalue=="教育"){
           this.picvalue=4
        }if(this.picvalue=="金融"){
          this.picvalue=5
        }if(this.picvalue=="游戏"){
           this.picvalue=6
        }if(this.picvalue==''){
          this.picvalue=''
        }
          this.$axios.put('api/a/u/article/'+this.id,
          this.$qs.stringify({
            
              title:this.title,
              status:1,
              img:this.imgurl,
              url:this.link,
              createAt:this.createAt,
              type:this.typevalue,
              industry:this.picvalue,
              content:this.desc,
          }))
          .then(res=>{
            console.log(res)
            
          })
          .catch(err=>{
            console.log(err)
          })
        this.skipLink = "ArticleList"
        this.$alert('编辑草稿成功', {
          confirmButtonText: '确定',
        })

        }else{
             if(this.title!=''&&this.typevalue!=''&&this.imgurl!=''&&this.desc!=''&&this.link!=''){
        this.$axios.post('api/a/u/article',
        this.$qs.stringify({
        title:this.title,
        type:this.typevalue,
        status:this.draftstatus,
        img:this.imgurl,
        content:this.desc,
        url:this.link,
        industry:this.picvalue,
        }))
        .then(res=>{
          console.log(res)
         
        })
        .catch(err=>{
          console.log(err)
        })
        this.skipLink = "ArticleList"
         this.$alert('新增草稿成功', {
          confirmButtonText: '确定',
        });
        }else{
           this.$alert('请完整的填写信息再提交', {
          confirmButtonText: '确定',
        })
        }
        }
     
      },
      //取消的点击按钮
      cancel(){
        console.log('取消按钮')
      },
    
      //退出登录
      loginout(){
        this.$axios.post('api/a/logout',)
        .then(res=>{
          console.log(res)
          this.$router.push({path:'/Login'})
        })
        .catch(err=>{
          console.log(err)
        })
      }

       //限制文件的格式和大小，这里暂时先不用，需要的时候再用
      // beforeAvatarUpload(file) {
      //   const isJPG = file.type === 'image/jpeg';
      //   const isPNG = file.type === 'image/png';
      //   const isLt2M = file.size / 1024 / 1024 < 2;

      //   if (!isJPG && !isPNG) {
      //     this.$message.error('上传头像图片只能是 JPG或者PNG 格式!');
      //   }
      //   if (!isLt2M) {
      //     this.$message.error('上传头像图片大小不能超过 2MB!');
      //   }
      //   return isJPG && isLt2M && isPNG;
        
      // },
    },
    created() {
      console.log("我是created")
      New.$on('getEditData',data=>{
        console.log(data)
        this.title = data.title
        this.typevalue =data.type
        this.desc = data.content
        this.link = data.url
        this.imgurl = data.img
        this.picvalue = data.industry
        this.createAt = data.createAt
        this.id = data.id
      }),
      console.log(this.user)
      console.log(this.role)
    },
  
    // computed: {
    //   aaa(val){
    //       if(this.typevalue===0){
    //       return this.typevalue="首页HomePage"
    //     }if(val==1){
    //       return val="找职位SeekJob" 
    //     }if(val==2){
    //       return val="找精英Find the elite"
    //     }if(this.typevalue===3){
    //       return this.typevalue="行业大图"
    //     }
    //   }
    // },
    filters:{
      typeFilter(val){
        if(val==0){
          return val="首页HomePage"
        }if(val==1){
          return val="找职位SeekJob" 
        }if(val==2){
          return val="找精英Find the elite"
        }if(val==3){
          return val="行业大图"
        }
      },
      industryFilter(val){
        if(val==0){
          return val="移动互联网"
        }if(val==1){
          return val="电子商务"
        }if(val==2){
          return val="企业服务"
        }if(val==3){
          return val="O2O"
        }if(val==4){
          return val="教育"
        }if(val==5){
          return val="金融"
        }if(val==6){
          return val="游戏"
        }
      }
    }
  }

</script>

<style scoped>
  div {
    width: 100%;
  }
  .header {
    text-align: right;
    font-size: 12px;
    background: #fbfbfb;
  }

  .Article-add {
    border: 1px solid#c4c4c4;
    margin: 0 2rem;
    padding-left: 10px;
    font: bold;
  }

  .radius {
    margin: -2px 2rem;
    border-radius: 4px;
    border: 1px solid #c4c4c4;
    border-top: 0;
  }

  .inputWidth {
    width: 70%;
    margin: 1rem auto;
  }

  /* .el-form-item {
    text-align: center;
  } */
  .bottom-btn-center {
    text-align: center;
    /* background: #3278b5; */
    display: flex;
    justify-content: space-around;
   
  }

  .left-button {
    background: #94d094;
    border: 1px solid#94d094;
    /* margin-right: 120px; */
  }

  .handleName {
    display: flex;
    justify-content: space-around;
    font-weight: bold;
  }
  .handleStyle{
     display: flex;
    justify-content: space-around;
    border-top: 2px solid#ddd;
  }
  .successIcon{
   margin-top: 15px;
  }
  .imgurl{
    width: 100%;
    margin-top: 15px;
  }
</style>


